@import url("index.css");

.about {
    max-width: 600px;
    padding: 300px 130px 20px;
    margin-left: 500px;
    margin-right: -500px;
}

.polaroid {
    width: fit-content; /* Adjust to the image size */
    padding: 10px 10px 30px; /* Space at the bottom for the caption */
    background-color: white;
    transform: scale(0.5);
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5);
    border: 1px solid #ddd;
    display: inline-block;
    margin: 20px;
    text-align: center;
    font-family: 'Polaroid', sans-serif; 
    position: absolute;  
    top: -95px;
    left: 80px;
    
}
  
.polaroid-image {
    display: block;
    transform: scale(0.9);
    max-width: 100%; /* Ensures the image fits within the container */
    height: 100%; /* Maintains the image aspect ratio */
}
  
.caption {
    font-size: 14px;
    color:#3D52A0;
    background-color: white;
    margin-top: -10px; /* Space between the image and caption */
    font-family: 'Polaroid', sans-serif;
    font-size: 60px;
    /* Typing Animation */
    display: inline-block;
    font-weight: 500;
    white-space: nowrap;
    margin: 0 auto;
    overflow: hidden;
    border-right: 1px solid #3D52A0;
    animation: typing 4s steps(20, end) infinite, blink-caret 0.75s step-end infinite;
}

.socials-container {
  margin-top: 15px;
  margin-right: 130px;
}

.horizontal-stack {
    display: flex;
    flex-direction: row;
}

.vertical-stack {
    display: flex;
    flex-direction: column;
}

.about h1 {
    font-size: 50px;
    font-weight: bold;
    margin-bottom: 10px;
    text-shadow: 2px 2px 4px #828282;
}

#about .p1 {
    font-size: 25px;
    line-height: 1; 
    margin-bottom: 15px; 
}

#about .p2 {
    font-size: 20px;
    line-height: 1; 
    margin-bottom: 15px; 
}

.link-button {
    font-size: 25px !important; 
    font-family: "Quicksand";
    font-weight: normal; 
    text-decoration: none;
    border-radius: 20px !important;
    border-color: #3D52A0;
    background: transparent;
    transition: background 0.3s, transform 0.3s, box-shadow 0.3s;
    display: flex !important;
    flex-direction: row !important;
    justify-content: center !important;
    align-items: center !important;
    padding: 10px 30px !important;
    margin-right: 25px !important; /* Space between buttons */
    position: relative !important; /* Ensure proper positioning */
    overflow: hidden !important; /* Prevent background from overflowing */
    color: #333;
}

.link-button img {
    margin-right: 15px;
}

a {
    text-decoration: none;
}

.link-button:hover {
    background: rgba(255, 255, 255, 0.2);
    color: #3D52A0;
    transform: scale(1.05); /* Scale up on hover */
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.5); /* Adds shadow on hover */
}